@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,300;0,400;0,500;0,600;0,700;1,400&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Raleway', sans-serif;
    
}
#root{
    display: grid;
    grid-template-columns: minmax(auto, 1fr) minmax(auto, 351px);
    grid-template-rows: minmax(auto, 80px) minmax(auto,700px);
    row-gap: 30px;
    padding-left: 50px;
    column-gap: 10px;
}
header{
    width: -webkit-fill-available;
    gap: 20px;
    justify-content: space-between;
}
header h1{
    font-weight: 700;
    font-size: 37px;
    line-height: 43px;
}
header .location{
    text-overflow: ellipsis; 
    overflow: hidden; 
    white-space: nowrap;
    margin-left: 5px;
    width: 160px;
}
header .input-wrap{
    position: relative;
    width: 50%;
    min-width: 260px;
    border: 1px solid #F0F4FB;
    border-radius: 36px;
}
header .input-wrap input{
    width: 100%;
    font-weight: 400;
    font-size: 16px;
    padding: 15px 70px 16px 20px;
    outline: 0;
    border: 0;
    background: transparent;
}
header .btn-search{
    padding: 9px 37px;
    border: 0;
    border-radius: 41px;
    position: absolute;
    cursor: pointer;
    right: 4px;
    top:4px;
    background: #F0F4FB;
}
header .btn-cart-wrap{
    position: relative;
}
.cart-avatar-container{
    display: flex;
    justify-content: space-around;
    width: 20%;
    min-width: 150px;
}
header .btn-cart-wrap .cart-amount{
    position: absolute;
    top: -10px;
    right: -10px;
    font-weight: 600;
    font-size: 14px;
    line-height: 140%;
    color: #2967FF;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 31px;
    height: 31px;
    text-align: center;
    background: #fff;
    border-radius: 20px;
}
header .btn-cart{
    cursor: pointer;
    background: transparent;
    border: 0;
}
header .avatar{
    min-width: 50px;
    height: 50px;
    border-radius: 30px;
    background-color: #a13f06;
}
header, .location-wrap, .same-banners, main .old-price-wrap{
    display: flex;
    align-items: center;
}
aside, main{
    font-feature-settings: 'pnum' on, 'lnum' on;
}
.top-banner{
    max-width: 311px;
    min-width: 260px;
    height: 143px;
    border: 1px solid #FF2D87;
    border-radius: 20px;
    display: flex;
    align-items: center;
}
.top-banner .bag-wrap{
    position: relative;
}
.top-banner .bag-wrap svg{
}
.top-banner-text-wrap{
    height: 100px;
    display: grid;
    justify-items: start;
    align-items: center;
}
.top-banner h2{
    font-size: 20px;
    font-weight: 600;
    line-height: 130%;
    color: #2967FF;
}
.top-banner .sale-info{
    background: #2967FF;
    color: #fff;
    border-radius: 51px;
    padding: 11px 25px 10px;
    outline: 0;
    border: 0;
}
.same-banners{
    max-width: 311px;
    height: 168px;
    background: no-repeat;
    text-align: center;
    border-radius: 20px;
    margin: 20px 0;
    justify-content: center;
}
.same-banners h2{
    color: #fff;
    text-align: center;
    font-weight: 700;
    font-size: 20px;
    width: 170px;
}
main nav{
    gap: 107px;
    display: flex;
    align-items: baseline;
}
main nav a{
    text-decoration: none;
    font-weight: 600;
    font-size: 12px;
    color: #2967FF
}
main .tag-wrap{
    display: flex;
    margin-top:20px;
    gap: 5px;
}
main .tag{
    font-weight: 600;
    font-size: 14px;
    line-height: 130%;
    color: #fff;
}
main .tag:last-child{
    color: black;
    border: 1px solid #FF2D87;
}
main .tag{
    padding: 2px 10px;
    border-radius: 40px;
}
main .products-container{
    margin-top: 20px;
    display: flex;
    gap: 20px;
    row-gap: 30px;
    flex-wrap: wrap;
}
main figure img {
    width: 100%;
}
main figure{
    width: 214px;
}
main figure figcaption{
    font-weight: 400;
    font-size: 14px;
    -webkit-line-clamp: 2;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    overflow: hidden;
    line-height: 140%;
}
main figure .new-price{
    font-weight: 600;
    margin-top: 5px;
    font-size: 20px;
    color:#2967FF;
    line-height: 130%;
}
main .old-price-wrap{
    gap: 15px;
}
main .discount{
    color: #FF2D87;
    font-weight: 600;
    font-size: 14px;
    line-height: 140%;
}
main figure .old-price{
    font-weight: 400;
    font-size: 14px;
    line-height: 122%;
    text-decoration: line-through;
    color: #8D8D8E;
}
main figure .btn-add-to-cart{
    margin-top: 10px;
    background: transparent;
    color: #2967FF;
    padding: 10px 40px;
    border: 1px solid #2967FF;
    border-radius: 26px;
}

@media screen and (max-width:1070px) {
    header{
        flex-wrap: wrap;
        height: 200px;
        justify-content: space-evenly;
    }
    .tag-wrap{
        flex-wrap: wrap;
    }
    main nav{
        gap: 40px;

    }
    main nav a{
        margin-right: 10px;
    }
}